<script>
import axios from "axios";
import {URL_GET_WALLET_URL} from "@pages/store/config";

export default {
  name: "Wallet",
  props: {
    priceProp: {
      type: Number,
      default: 10,
      required: true
    },
  },
  data() {
    return {
      address: '',
      endTime: '',
      timeLeft: null,
      error: false,
      loaded: false
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  watch: {
    timeLeft(el) {
      if (el == null) {
        this.getWallet();
      }
    }
  },
  created() {
    this.getWallet();
  },
  methods: {
    updateTimer() {
      const now = new Date();
      const diff = this.endTime.getTime() - now.getTime();
      if (diff > 0) {
        const hours = Math.floor(diff / 3600000).toString().padStart(2, '0');
        const mins = Math.floor((diff % 3600000) / 60000).toString().padStart(2, '0');
        const secs = Math.floor((diff % 60000) / 1000).toString().padStart(2, '0');
        this.timeLeft = `${hours}:${mins}:${secs}`;
      } else {
        this.timeLeft = null;
        clearInterval(this.timer);
      }
    },
    getWallet: function () {
      axios.get(URL_GET_WALLET_URL, {
        '_csrf': $('meta[name="csrf-token"]').attr('content')
      }).then(response => {
        this.parseResponse(response.data)
      }).catch(error => {
        alert(error)
        this.closeModal();
      });
    },
    parseResponse(resp) {
      if (resp.success) {
        if (resp.address) {
          this.address = resp.address;
          const now = new Date();
          now.setTime(now.getTime()+resp.end_date);
          this.endTime = now;
          this.timer = setInterval(this.updateTimer, 1000);
        }
      }
      if (resp.error) {
        this.error = resp.error
      }
      this.loaded = true;
    },
    closeModal() {
      this.emitter.emit('balanceModal');
    },
    showCheckAmount() {
      this.emitter.emit('setActiveModal', {setActiveComponent: 'CheckAmount'});
    },
    showPaymentMethodModal() {
      this.emitter.emit('setActiveModal', {setActiveComponent: 'PaymentMethods'});
    },
    copyToClipboard() {
      navigator.clipboard.writeText(this.address)
          .then(() => {
            alert('Tether-адрес скопирован в буфер обмена!');
          })
          .catch((error) => {
            alert('Ошибка при копировании в буфер обмена!');
          });
    }
  }
}
</script>
