.modal-content {
  &.check-amount {
    .table_banner_wrap {
      width: auto;
      margin-bottom: 0;
      margin-top: 20px;
    }

    .btn-select-payment {
      @extend %btn;
      padding: 16px;
      background-color: $color-yellow-balance;
    }

    .wrap-costs {
      display: flex;
      width: 100%;
      gap: 12px;

      .btn-costs {
        @extend %btn;
        padding: 12px;
        background-color: $color-grey-balance;
        border: 1px solid rgba(96, 102, 124, 0.1);
      }
    }

    .set-price {
      width: 100%;
      border-bottom: 1px solid;
      padding: 16px 0;
      display: flex;
      justify-content: space-between;

      input {
        width: 100%;
        outline: none;

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
          margin: 0;
        }

        &[type="number"] {
          -moz-appearance: textfield;
        }
      }

      .currency {
        padding: 9px 16px;
        background-color: $color-grey-balance;
        border-radius: 4px;
      }
    }

    .text-payment, .errors {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #60667C;
      margin-top: 32px;
    }

    .errors {
      color: #E73939;
      margin-top: 8px;
      display: flex;

      svg {
        margin-right: 5px;
      }
    }

    min-width: 500px;
    @media (max-width: 768px) {
      min-width: auto;
    }
  }
}