$color-yellow-balance: #FCDF55;
$color-grey-balance: #DFE8F4;
$coloryellowbalance: #FCDF55;
%btn{
	font-weight: 500;
	font-size: 16px;
	line-height: 24px;
	color: #000000;
	border-radius: 12px;
	width: 100%;
	margin-top: 32px;
	&.disabled{
		opacity: 0.5;
	}
}

.user-balance{
	font-size: .973vw;
	color: $coloryellowbalance;
	display: flex;
	flex-direction: row;
	@media (max-width: 1100px){
		font-size: 10px;
	}
	@media (max-width: 768px){
		font-size: 12px;
	}
	span{
		display: flex;
		align-items: center;
	}
	span, button{
		font-weight: 500;
	}
	button{
		margin-left: 8px;
		border: 1px $coloryellowbalance solid;
		border-radius: 25px;
		padding: 8px 16px;
		@media (max-width: 1100px){
			display: none;
		}
	}
}

.modal-wrap{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 9999;
	display: flex;
	justify-content: center;
	align-items: center;
	.balance-modal-header{
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		span{
			font-weight: 500;
			font-size: 24px;
			line-height: 32px;
			color: #0E2136;
		}
	}
	.modal-balance-overlay{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.5);
	}
	.modal-content{
		max-width: 50%;
		background-color: white;
		padding: 1rem;
		display: flex;
		justify-content: center;
		flex-direction: column;
		z-index: 3;
		padding: 32px;
		border-radius: 12px;
		@media (max-width: 768px){
			min-width:auto;
			max-width: 90vw;
		}
	}
	@import "partial/checkAmount";
	@import "partial/paymentMethod";
	@import "partial/paymentPlan";
	@import "partial/wallet";
	@import "partial/paymentBot";
}
