.waller-model-wrap{
	position: relative;
	overflow-y: scroll;
	height: 100%;
	width: 100%;
	.modal-content{
		&.wallet{
			position: relative;
			padding: 0;
			justify-content: unset;
			overflow: auto;
			margin: 0 auto;
			margin-bottom: 50px;
			margin-top: 50px;
			.wrap{
				padding: 32px 32px 0 32px;
				.title{
					font-weight: 500;
					font-size: 20px;
					line-height: 32px;
				}
				.video{
					margin-top: 32px;
					border-radius: 8px;
					background: #F1F6F8;
					padding: 12px 16px;
					display: flex;
					justify-content: space-between;
					.text{
						.subtitle{
							margin-top: 4px;
							font-weight: 400;
							font-size: 16px;
							line-height: 24px;
						}
					}
				}
				.error{
					margin-top: 32px;
					margin-bottom: 32px;
					border-radius: 8px;
					background: #F1F6F8;
					padding: 12px 16px;
					display: flex;
					flex-direction: column;
					.text-wrap{
						display: flex;
						.icon{
							display: flex;
							align-items: center;
							margin-right: 12px;
						}
					}
					button{
						margin-top: 24px;
						border-radius: 12px;
						font-weight: 500;
						font-size: 16px;
						line-height: 24px;
					}
					.btn-pay{
						padding: 16px 41px;
						background-color: $color-yellow-balance;
					}
				}
				.balance{
					margin-top: 32px;
					display: flex;
					justify-content: space-between;
					.link{
						text-decoration: none;
						color: #4C84EF;
						cursor: pointer;
					}
				}
				.small_title{
					font-weight: 400;
					font-size: 16px;
					line-height: 24px;
				}
				.sub_desc{
					margin-top: 24px;
					color: #60667C;
					a{
						color: #4C84EF;
					}
					&.small_title{
						text-align: center;
					}
				}
				.desc{
					color: #0E2136;
					word-break: break-word;
					margin-top: 12px;
					font-weight: 500;
					font-size: 18px;
					line-height: 28px;
				}
				.address-wrap{
					padding-top: 12px;
					display: flex;
					justify-content: center;
					gap: 15px;
					.address{
						padding: 10px 0;
						border-bottom: 1px solid black;
						font-size: 16px;
					}
					.copy{
						cursor: pointer;
						padding: 12px 17px;
						border: 1px solid rgba(96, 102, 124, 0.1);
						background-color: #E0E9F5;
						border-radius: 4px;
					}
				}
			}
			.bottom-wrap{
				background: #F1F6F8;
				border-radius: 8px;
				margin-top: 32px;
				padding: 24px 32px;
				display: flex;
				justify-content: left;
				.icon{
					margin-right: 12px;
					display: flex;
					align-items: center;
					img{
						max-height: 100px;
					}
				}
				.text-wrap{
					.title{
						font-weight: 500;
						font-size: 18px;
						line-height: 24px;
						color: rgba(96, 102, 124, 1);
					}
					.desc{
						font-weight: 300;
						font-size: 16px;
						line-height: 24px;
						color: rgba(14, 33, 54, 1);
					}
				}
			}
			.warning-timer{
				background-color: #ff634729;
				border-radius: 10px;
				padding: 10px;
				.timer-info{
					margin-bottom: 10px;
					display: flex;
					.warning-icon{
						svg{
							width: 70px;
							margin-right: 10px;
						}
					}
					.timer-desc{
						text-align: left;
					}
				}
			}
			.timer{
				margin-left: 10px;
				margin-top: 15px;
				text-align: center;
				font-size: 18px;
			}
			@media (min-width: 1200px){
				.wrap{
					.address-wrap{
						.address{
							&.small_title{
								font-size: 22px;
							}
						}
					}
				}
			}
		}
	}
}