.modal-content{
	&.payment{
		.wrap{
			margin-top: 32px;
			.payment-wrap{
				background: #F1F6F8;
				border-radius: 8px;
				padding: 12px 16px;
				.item{
					display: flex;
					justify-content: space-between;
				}
			}
			.payment-text-wrap{
				margin-top: 24px;
				.text-message{
				}
				.title{
					margin-top: 8px;
				}
			}
			&.wrap-success{
				min-width: 25vw;
				margin-top: 32px;
				border-radius: 8px;
				background: #F1F6F8;
				padding: 12px 16px;
				display: flex;
				flex-direction: column;
				.text-wrap{
					display: flex;
					flex-direction: column;
					.icon{
						display: flex;
						align-items: center;
						margin-top: 18px;
						font-size: 60px;
						justify-content: center;
					}
					.text{
						margin-top: 24px;
						text-align: center;

					}
				}
				button{
					margin-top: 24px;
					border-radius: 12px;
					font-weight: 500;
					font-size: 16px;
					line-height: 24px;
				}
				.btn-pay{
					padding: 16px 41px;
					background-color: $color-yellow-balance;
				}
			}
		}
		.button-warp{
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-column-gap: 16px;
			gap: 16px;
			margin-top: 32px;
			@media (max-width: 560px){
				grid-template-columns: 1fr;
			}
			button{
				border-radius: 12px;
				font-weight: 500;
				font-size: 16px;
				line-height: 24px;
				&:first-of-type{
					border: 1px solid;
					padding: 16px;
				}
			}
			.btn-pay{
				padding: 16px 41px;
				background-color: $color-yellow-balance;
			}
		}
		.font-amount-bold{
			font-weight: 500;
			font-size: 20px;
			line-height: 32px;
			&.red{
				color: #E73939;
			}
		}
		.font-title{
			font-weight: 400;
			font-size: 20px;
			line-height: 32px;
		}
	}
}