.modal-content{
	&.payment-methods{
		.methods-wrap{
			margin-top: 32px;
			.item{
				border: 1px solid #DDDDDD;
				border-radius: 16px;
				padding: 16px;
				.item-wrap{
					.title{
						font-weight: 700;
						font-size: 16px;
						line-height: 24px;
						position: relative;
						display: inline-block;
						cursor: pointer;
						padding-left: 25px;
						margin-right: 8px;
						&:hover{
							cursor: pointer;
						}
					}
					.icons-payment{
						margin-top: 16px;
					}
				}
			}
			.interkassa{
				.icons-payment{
					display: grid;
					grid-template-columns: repeat(auto-fill, minmax(48px, 1fr));
					grid-gap: 8px;
				}
			}
			.wallet{
				margin-top: 16px;
			}
			@media (max-width: 992px){
				.interkassa .icons-payment, .icons-payment{
					display: none;
				}
			}
		}
		.button-warp{
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-column-gap: 16px;
			gap: 16px;
			margin-top: 32px;
			@media (max-width: 560px){
				grid-template-columns: 1fr;
			}
			button{
				border-radius: 12px;
				font-weight: 500;
				font-size: 16px;
				line-height: 24px;
			}
			.btn-amount{
				border: 1px solid;
				padding: 16px;
			}
			.btn-pay{
				padding: 16px 41px;
				background-color: $color-yellow-balance;
			}
		}
		radio-wrapper{
			position: relative;
			display: inline-block;
			cursor: pointer;
			padding-left: 25px;
			margin-right: 10px;
		}
		.radio-custom{
			position: absolute;
			top: 2px;
			left: 0;
			width: 20px;
			height: 20px;
			border: 2px solid #000000;
			border-radius: 50%;
			&::before{
				content: '';
				position: absolute;
				background-color: #000000;
				width: 10px;
				height: 10px;
				display: none;
				border-radius: 5px;
				text-align: center;
				left: 3px;
				top: 3px;
			}
		}
		input[type="radio"]:checked + .radio-custom{
			border-color: #333;
			&::before{
				display: block;
			}
		}
		input[type="radio"]{
			position: absolute;
			opacity: 0;
		}
	}
}